body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}
.main{
  width: 100%;
  min-height: 100vh;
  background: #232323;
}
header{position:relative;width:100%;padding:10px;display:flex;z-index:10000;position:fixed;max-height:60px;align-items:center;font-family:"Maven Pro",sans-serif;justify-content:space-between;background:#111}header .toggle-menu{position:absolute;right:50px}header .logo{color:#fff;font-size:35px;padding-left:30px;margin:0}header .logoGallery{font-size:20px;margin-left:7px;font-weight:200;color:#ff9d00}header .navigation{padding-right:150px;display:flex;justify-content:center;align-items:center}header .navigation .navLink{display:block;color:#cfcfcf;text-decoration:none;font-size:20px;padding:0 5px;position:relative;margin-left:25px;transition:.3s}header .navigation .navLink::before{content:"";position:absolute;left:0;bottom:-3px;width:0%;height:3px;transition:.25s;background:#fff}header .navigation .navLink:hover::before{width:100%}header .navigation .navLink:hover{color:#fff}header .navigation .activeNavLink{color:#ff9d00}
.responsiveNavigation{position:fixed;height:0;width:100%;overflow:hidden;transition:1s;background:#111;margin-top:60px;-webkit-transform-origin:top;transform-origin:top;color:#fff;z-index:100000}.responsiveNavigation .navLinkRes{display:block;color:#cfcfcf;text-decoration:none;width:85%;text-align:center;font-size:20px;padding:10px;position:relative;margin-left:25px;transition:.3s}.responsiveNavigation .navLinkRes:hover{background:#e99001;color:#111}.responsiveNavigation .activeNavLinkRes{background:#925a00;color:#111}.responsiveNavigation .activeNavLinkRes:hover{background:#e99001}
.gallery{width:85%;display:grid;grid-gap:15px;grid-auto-rows:minmax(300px, auto);grid-template-columns:repeat(3, 1fr)}
.oneImage{width:100%;height:100%;transition:.375s;-webkit-filter:brightness(0.6);filter:brightness(0.6);object-fit:cover}.containerImage{cursor:pointer;position:relative;overflow:hidden}.containerImage:hover img{-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-filter:brightness(1);filter:brightness(1)}
.loader{display:flex;justify-content:center;align-items:center;position:fixed;min-height:100vh;top:0;left:0;width:100%;z-index:1000;background:#1b1b1b}
.containerHome{display:flex;justify-content:center;align-items:center;flex-direction:column;font-family:"Poppins",sans-serif}.containerHome .titleBox{margin-top:50px}.containerHome .titleBox .welcome{text-align:center;margin:50px 0;color:#fff;font-size:80px}.containerHome .titleBox .welcome span{color:#ff9d00}@-webkit-keyframes welcome{from{-webkit-transform:scaleX(0);transform:scaleX(0)}to{-webkit-transform:scaleX(1);transform:scaleX(1)}}@keyframes welcome{from{-webkit-transform:scaleX(0);transform:scaleX(0)}to{-webkit-transform:scaleX(1);transform:scaleX(1)}}
.imageDetail{display:flex;justify-content:center;align-items:center;flex-direction:column}.imageDetail .imageTitle{margin:20px 0;padding-top:30px;font-size:70px;-webkit-animation:hello 1s;animation:hello 1s;margin:50px 0}.imageDetail .image{max-height:600px;max-width:900px}.imageDetail .imageId{font-size:25px;color:#444}.imageDetail button{height:50px;width:150px;margin:20px 10px;border:none;border-radius:10px;color:#000;font-size:25px;cursor:pointer;font-family:"Poppins",sans-serif;background:#fff;outline:none;box-shadow:0 7px 0 #888}.imageDetail button a{color:#000;text-decoration:none}.imageDetail button:active{box-shadow:inset 0 0 10px #00000020;-webkit-transform:translateY(7px);transform:translateY(7px)}.imageDetail .downloadIcon{margin-top:10px;font-size:30px}@-webkit-keyframes hello{0%{-webkit-transform:scale3d(1, 1, 1);transform:scale3d(1, 1, 1)}30%{-webkit-transform:scale3d(1.25, 0.75, 1);transform:scale3d(1.25, 0.75, 1)}40%{-webkit-transform:scale3d(0.75, 1.25, 1);transform:scale3d(0.75, 1.25, 1)}50%{-webkit-transform:scale3d(1.15, 0.85, 1);transform:scale3d(1.15, 0.85, 1)}65%{-webkit-transform:scale3d(0.95, 1.05, 1);transform:scale3d(0.95, 1.05, 1)}75%{-webkit-transform:scale3d(1.05, 0.95, 1);transform:scale3d(1.05, 0.95, 1)}100%{-webkit-transform:scale3d(1, 1, 1);transform:scale3d(1, 1, 1)}}@keyframes hello{0%{-webkit-transform:scale3d(1, 1, 1);transform:scale3d(1, 1, 1)}30%{-webkit-transform:scale3d(1.25, 0.75, 1);transform:scale3d(1.25, 0.75, 1)}40%{-webkit-transform:scale3d(0.75, 1.25, 1);transform:scale3d(0.75, 1.25, 1)}50%{-webkit-transform:scale3d(1.15, 0.85, 1);transform:scale3d(1.15, 0.85, 1)}65%{-webkit-transform:scale3d(0.95, 1.05, 1);transform:scale3d(0.95, 1.05, 1)}75%{-webkit-transform:scale3d(1.05, 0.95, 1);transform:scale3d(1.05, 0.95, 1)}100%{-webkit-transform:scale3d(1, 1, 1);transform:scale3d(1, 1, 1)}}
.containerForm{width:100%;position:relative;max-height:100vh;display:flex;justify-content:center;align-items:center;font-family:"Poppins",sans-serif}.containerForm .form{margin-top:130px;height:700px;width:1000px;position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;background:#121212}.containerForm .form .title{top:100px;position:absolute;text-align:center;color:#ff9d00;font-size:60px}.containerForm .form form{display:flex;justify-content:center;flex-direction:column;min-width:500px;min-height:700px}.containerForm .form form .uploadLabel{margin:15px 0px;height:40px;height:60px;width:60px;display:flex;justify-content:center;align-items:center;padding:5px 30px;border:3px solid #fff;transition:.3s;cursor:pointer}.containerForm .form form .uploadLabel i{transition:.3s;font-size:30px}.containerForm .form form .uploadLabel:hover{background:#fff;color:#000}.containerForm .form form .uploadLabel:hover i{color:#000}.containerForm .form form button{display:block;margin-top:50px;height:40px;width:250px;font-size:25px;border:2.5px solid #fff;transition:.3s;background:transparent;font-family:"Poppins",sans-serif;color:#ff9d00}.containerForm .form form button:hover{background:#fff;-webkit-transform:translateY(-5px);transform:translateY(-5px);box-shadow:10px 10px 10px #00000050}

.inputContainer{display:block;margin:40px 0}.inputContainer .inputBox{position:relative}.inputContainer .inputBox .labelComponent{position:absolute;top:0;left:0;font-size:25px;left:30px;transition:.3s}.inputContainer .inputBox .inputComponent{position:absolute;top:0;left:0;height:40px;width:350px;display:block;outline:none;font-size:25px;border:3px solid #fff;color:#fff;transition:.4s;background:transparent}.inputContainer .inputBox .inputComponent:focus{background:#fff;color:#000;width:500px}.inputContainer .inputBox .inputComponent:focus+.labelComponent{top:-30px;left:0;font-size:20px}.inputComponent:not(:-ms-input-placeholder).inputComponent:not(:focus)+.labelComponent{top:-30px;left:0;font-size:20px;color:#ffffff70}.inputComponent:not(:placeholder-shown).inputComponent:not(:focus)+.labelComponent{top:-30px;left:0;font-size:20px;color:#ffffff70}
.containerError{display:flex;justify-content:center;align-items:center;flex-direction:column;min-height:100vh;width:100%}.containerError .error{font-size:300px}.containerError .error span{display:inline-block;color:#ff9d00;-webkit-animation:movementSpan 2s infinite ease-in-out;animation:movementSpan 2s infinite ease-in-out;animation-direction:alternate-reverse}.containerError .error img{height:270px;width:270px;-webkit-animation:movement 2s infinite ease-in-out;animation:movement 2s infinite ease-in-out}.containerError .msg{font-size:50px;font-weight:800}@-webkit-keyframes movement{0%,100%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}50%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes movement{0%,100%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}50%{-webkit-transform:translateY(0);transform:translateY(0)}}@-webkit-keyframes movementSpan{0%,100%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}@keyframes movementSpan{0%,100%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}
.containerForm{width:100%;position:relative;max-height:100vh;display:flex;justify-content:center;align-items:center;font-family:"Poppins",sans-serif}.containerForm .formBox{margin-top:130px;height:700px;width:1000px;position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;background:#121212}.containerForm .formBox .title{font-size:70px;color:#ff9d00;position:absolute;top:70px}.containerForm .formBox form{display:flex;justify-content:center;flex-direction:column}.containerForm .formBox form .btnSubmit{width:350px;height:50px;color:#ff9d00;text-align:center;border:3px solid #fff;background:transparent;transition:.3s;margin:50px auto auto auto;font-size:25px;font-family:"Poppins",sans-serif}.containerForm .formBox form .btnSubmit:hover{background:#fff;-webkit-transform:translateY(-5px);transform:translateY(-5px)}
